import React, { useRef, useState } from 'react';
import { View, Text, StyleSheet, Platform, Image, Alert } from 'react-native';
import PDFGenerator from '../PDFGenerator';

// Helper Components
const LabelItem = ({ children }) => (
  <View style={styles.labelItem}>
    <Text style={styles.labelText}>{children}</Text>
  </View>
);

const BorderedItem = ({ children, price, style, isCutLengths }) => (
  <View style={[styles.borderedItem, style]}>
    {isCutLengths ? (
      <View style={styles.cutLengthsLayout}>
        <View style={styles.cutLengthsContent}>
          {children}
        </View>
        {price && (
          <View style={styles.cutLengthsPriceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </View>
    ) : (
      <>
        <Text style={styles.itemText}>{children}</Text>
        {price && (
          <View style={styles.priceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </>
    )}
  </View>
);

const NumberedCircle = ({ number }) => (
  <View style={styles.numberedCircle}>
    <Text style={styles.numberedCircleText}>{number}</Text>
  </View>
);

/**
 * Custom hook for generating PDFs from order configurations
 * @param {Object} params
 * @param {Array} params.cartItems - Current cart items
 * @param {Function} params.cleanPriceString - Function to clean price strings
 * @param {Function} params.calculatePricesFromData - Function to calculate prices from order data
 * @param {Function} params.getTotalLengthFromData - Function to get total hose length
 * @returns {Object} PDF generation functions and state
 */
export const useOrderPDFGeneration = ({ 
  cartItems,
  cleanPriceString,
  calculatePricesFromData,
  getTotalLengthFromData,
}) => {
  const [isGeneratingPDF, setIsGeneratingPDF] = useState(false);
  const [currentOrderForPDF, setCurrentOrderForPDF] = useState(null);
  const contentRef = useRef(null);

  // Initialize PDF generator
  const { generatePDF } = PDFGenerator({
    contentRef,
    orderId: `PWA-${Date.now()}`,
    
    onGenerateStart: () => {
      setIsGeneratingPDF(true);
    },
    
    onGenerateComplete: async (pdfBuffer) => {
      console.log(pdfBuffer);
      setIsGeneratingPDF(false);
    },
    
    onError: (error) => {
      console.error('PDF generation failed:', error);
      setIsGeneratingPDF(false);
      Alert.alert(
        'PDF Generation Failed',
        'Unable to generate order PDF. Please try again.',
        [{ text: 'OK' }]
      );
    }
  });

  /**
   * Generate PDF for a specific order
   * @param {Object} orderSnapshot - Order configuration data
   * @returns {Promise<string>} Base64 PDF data URL
   */
  const generateOrderPDF = async (orderSnapshot) => {
    if (Platform.OS !== 'web') {
      throw new Error('PDF generation only supported on web platform');
    }

    try {
      // Set the snapshot state (updates hidden PDF container)
      setCurrentOrderForPDF(orderSnapshot);
      
      // Wait for React to update the DOM
      await new Promise(resolve => setTimeout(resolve, 100));
      
      // Generate PDF
      setIsGeneratingPDF(true);
      const pdfBuffer = await generatePDF({ silent: true });
      
      if (!pdfBuffer) {
        throw new Error('PDF generation returned null');
      }

      // Convert ArrayBuffer to Base64 data URL
      const pdfBlob = new Blob([pdfBuffer], { type: 'application/pdf' });
      
      return new Promise((resolve, reject) => {
        const reader = new FileReader();
        
        reader.onloadend = () => {
          const base64PDF = reader.result;
          
          // Clear snapshot
          setCurrentOrderForPDF(null);
          setIsGeneratingPDF(false);
          
          resolve(base64PDF);
        };
        
        reader.onerror = () => {
          setCurrentOrderForPDF(null);
          setIsGeneratingPDF(false);
          reject(new Error('Failed to convert PDF to Base64'));
        };
        
        reader.readAsDataURL(pdfBlob);
      });
      
    } catch (error) {
      console.error('Error in generateOrderPDF:', error);
      setIsGeneratingPDF(false);
      setCurrentOrderForPDF(null);
      throw error;
    }
  };

  /**
   * Download all PWA order PDFs from cart
   */
  const downloadAllCartPDFs = async () => {
    console.log('Downloading PDFs from cart...');
    
    const pwaItems = cartItems.filter(item => item.type === 'pwa_order' && item.pdfDataUrl);
    
    if (pwaItems.length === 0) {
      Alert.alert('No PDFs', 'No PWA orders with PDFs found in cart.');
      return;
    }
    
    try {
      // Download each PDF with delay between downloads
      for (let i = 0; i < pwaItems.length; i++) {
        const item = pwaItems[i];
        console.log(`Downloading PDF ${i + 1}/${pwaItems.length}: ${item.id}`);
        
        // Convert Base64 data URL to Blob
        const base64Data = item.pdfDataUrl.split(',')[1];
        const byteCharacters = atob(base64Data);
        const byteNumbers = new Array(byteCharacters.length);
        
        for (let j = 0; j < byteCharacters.length; j++) {
          byteNumbers[j] = byteCharacters.charCodeAt(j);
        }
        
        const byteArray = new Uint8Array(byteNumbers);
        const blob = new Blob([byteArray], { type: 'application/pdf' });
        
        // Create download link
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `Order_${item.id}.pdf`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
        
        console.log(` Downloaded: Order_${item.id}.pdf`);
        
        // Delay between downloads
        if (i < pwaItems.length - 1) {
          await new Promise(resolve => setTimeout(resolve, 500));
        }
      }
      
      console.log(` All ${pwaItems.length} PDFs downloaded successfully!`);
      Alert.alert(
        'Success',
        `Downloaded ${pwaItems.length} PDF${pwaItems.length > 1 ? 's' : ''} from cart!`,
        [{ text: 'OK' }]
      );
      
    } catch (error) {
      console.error('Error downloading PDFs:', error);
      Alert.alert('Error', 'Failed to download one or more PDFs. Check console for details.');
    }
  };

  /**
   * Render order summary for PDF (hidden container)
   */
  const renderOrderSummaryForPDF = () => {
    if (!currentOrderForPDF) {
      return null;
    }

    const orderData = currentOrderForPDF;
    const orderTimestamp = Date.now();
    
    // Calculate prices using snapshot data
    const totalLength = getTotalLengthFromData(orderData.cutLengths);
    const calculatedPrices = calculatePricesFromData(orderData, totalLength);
    const displayPrice = orderData.isOrderFittingMode ? orderData.cutLengthPrice : calculatedPrices.total;
    
    return (
      <View style={styles.pdfContainer}>
        {/* Logo */}
        <View style={styles.pdfLogoContainer}>
          {Platform.OS === 'web' ? (
            <img
              src={require('../../assets/logo.png')}
              style={{
                width: '400px',
                height: '200px',
                objectFit: 'contain'
              }}
              alt="FluidPower Logo"
            />
          ) : (
            <Image
              source={require('../../assets/logo.png')}
              style={styles.pdfLogo}
              resizeMode="contain"
            />
          )}
        </View>

        {/* Order Details Box */}
        <View style={styles.detailsBox}>
          <View style={styles.boxHeader}>
            <Text style={styles.boxHeaderText}>Order Summary</Text>
          </View>
          
          <View style={styles.orderDetailsContainerPDF}>
            {/* Order Number */}
            <View style={styles.rowContainer}>
              <LabelItem>ORDER NUMBER</LabelItem>
              <BorderedItem>{`PWA-${orderTimestamp}`}</BorderedItem>
            </View>

            {/* Hose Size */}
            {orderData.selectedHose && (
              <View style={styles.rowContainer}>
                <LabelItem>HOSE SIZE</LabelItem>
                <BorderedItem>
                  {typeof orderData.selectedHose === 'object' && orderData.selectedHose.size 
                    ? orderData.selectedHose.size 
                    : orderData.selectedHose}
                </BorderedItem>
              </View>
            )}

            {/* Quantity */}
            {orderData.quantity && (
              <View style={styles.rowContainer}>
                <LabelItem>QUANTITY</LabelItem>
                <BorderedItem>{orderData.quantity}</BorderedItem>
              </View>
            )}

            {/* End 1 */}
            {orderData.end1Shape && orderData.end1Size && (
              <View style={styles.rowContainer}>
                <LabelItem>{orderData.isOrderFittingMode ? 'FITTING' : 'END1'}</LabelItem>
                <BorderedItem price={orderData.end1Price}>
                  {`${orderData.end1Shape} - ${orderData.end1Size}`}
                </BorderedItem>
              </View>
            )}

            {/* End 2 */}
            {orderData.end2Shape && orderData.end2Size && (
              <View style={styles.rowContainer}>
                <LabelItem>END2</LabelItem>
                <BorderedItem price={orderData.end2Price}>
                  {`${orderData.end2Shape} - ${orderData.end2Size}`}
                </BorderedItem>
              </View>
            )}

            {/* Angle */}
            {orderData.selectedAngle && (
              <View style={styles.rowContainer}>
                <LabelItem>ANGLE</LabelItem>
                <BorderedItem>{orderData.selectedAngle}</BorderedItem>
              </View>
            )}

            {/* Cut Lengths */}
            {orderData.cutLengths && orderData.cutLengths.length > 0 && (
              <View style={styles.rowContainer}>
                <LabelItem>CUT LENGTHS</LabelItem>
                <BorderedItem
                  price={calculatedPrices.baseHosePrice.toFixed(2)}
                  isCutLengths={true}
                >
                  <View style={styles.cutLengthsContentWrapper}>
                    {orderData.cutLengths.map((cut, i) => (
                      <View key={i} style={styles.cutLengthRow}>
                        <NumberedCircle number={i + 1} />
                        <Text style={styles.cutLengthText}>
                          {`${cut.length} ${cut.unit || 'mm'}`}
                        </Text>
                      </View>
                    ))}
                  </View>
                </BorderedItem>
              </View>
            )}

            {/* Protection */}
            {orderData.selectedProtection && (
              <View style={styles.rowContainer}>
                <LabelItem>PROTECTION</LabelItem>
                <BorderedItem 
                  price={orderData.selectedProtection === 'NOT REQUIRED' 
                    ? 'NOT REQUIRED' 
                    : orderData.protectionCost}
                >
                  {orderData.selectedProtection}
                </BorderedItem>
              </View>
            )}

            {/* Pressure Test */}
            {orderData.selectedPressure && (
              <View style={styles.rowContainer}>
                <LabelItem>PRESSURE TEST</LabelItem>
                <BorderedItem 
                  price={orderData.selectedPressure === 'Not Required' 
                    ? 'NOT REQUIRED' 
                    : orderData.pressureTestPrice}
                >
                  {orderData.selectedPressure}
                </BorderedItem>
              </View>
            )}

            {/* Total */}
            <View style={styles.totalContainerPDF}>
              <Text style={styles.totalLabelPDF}>Total Price (Ex. GST)</Text>
              <Text style={styles.totalValuePDF}>A${displayPrice.toFixed(2)}</Text>
            </View>
          </View>
        </View>

        {/* Footer */}
        <View style={styles.pdfFooter}>
          <Text style={styles.pdfFooterText}>
            For enquiries:  +61 409 517 333 | info@fluidpowergroup.com.au
          </Text>
          <Text style={styles.pdfFooterText}>
            FluidPower Group  {new Date().getFullYear()}
          </Text>
        </View>
      </View>
    );
  };

  /**
   * Render PDF content (hidden container for web)
   */
  const renderPDFContent = () => {
    if (Platform.OS !== 'web') {
      return null;
    }

    return (
      <div 
        ref={contentRef}
        style={{
          position: 'absolute',
          left: '-9999px',
          top: 0,
          width: '800px',
          backgroundColor: 'white'
        }}
      >
        {renderOrderSummaryForPDF()}
      </div>
    );
  };

  return {
    isGeneratingPDF,
    contentRef,
    generateOrderPDF,
    downloadAllCartPDFs,
    renderPDFContent,
  };
};

