import React from 'react';
import { View, Text, StyleSheet } from 'react-native';

// Helper Components for Screen Display
export const LabelItem = ({ children }) => (
  <View style={styles.labelItem}>
    <Text style={styles.labelText}>{children}</Text>
  </View>
);

export const BorderedItem = ({ children, price, style, isCutLengths }) => (
  <View style={[styles.borderedItem, style]}>
    {isCutLengths ? (
      <View style={styles.cutLengthsLayout}>
        <View style={styles.cutLengthsContent}>
          {children}
        </View>
        {price && (
          <View style={styles.cutLengthsPriceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </View>
    ) : (
      <>
        <Text style={styles.itemText}>{children}</Text>
        {price && (
          <View style={styles.priceBorder}>
            <Text style={styles.priceText}>
              {typeof price === 'number' ? `A$${price.toFixed(2)}` : price}
            </Text>
          </View>
        )}
      </>
    )}
  </View>
);

export const NumberedCircle = ({ number }) => (
  <View style={styles.numberedCircle}>
    <Text style={styles.numberedCircleText}>{number}</Text>
  </View>
);

export const TotalPriceDisplay = ({ total }) => (
  <View style={styles.totalPriceContainer}>
    <View style={styles.totalPriceLabelItem}>
      <Text style={styles.totalPriceLabelText}>TOTAL PRICE</Text>
    </View>
    <View style={styles.totalPriceValueContainer}>
      <Text style={styles.totalPriceValueText}>
        A${total ? total.toFixed(2) : '0.00'}
      </Text>
    </View>
  </View>
);

export const ShippingCostNotice = () => (
  <View style={styles.shippingNoticeContainer}>
    <Text style={styles.shippingNoticeText}>
      *All our shipping costs are capped at A$ 12.85
    </Text>
  </View>
);

/**
 * Renders the complete order details section for the screen
 */
export const OrderDetailsSection = ({
  orderDetails,
  isOrderFittingMode,
  end1Price,
  end2Price,
  protectionCost,
  pressureTestPrice,
  shippingCost,
  cutLengths,
  selectedHosePrice,
  getTotalLength,
  cleanPriceString,
}) => {
  const renderOrderDetails = () => {
    const details = [];
    
    if (orderDetails.hoseSize !== null) {
      details.push(['hoseSize', orderDetails.hoseSize]);
    }
    
    if (orderDetails.qty !== null) {
      details.push(['qty', orderDetails.qty]);
    }
    
    Object.entries(orderDetails).forEach(([key, value]) => {
      if (key !== 'hoseSize' && key !== 'qty' && value !== null) {
        details.push([key, value]);
      }
    });
    
    return details.map(([key, value], index) => {
      let label, content, price;

      switch (key) {
        case 'hoseSize':
          label = 'HOSE SIZE';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );

        case 'qty':
          label = 'QTY';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'end1':
          label = isOrderFittingMode ? 'FITTING' : 'END1';
          content = `${value.type} - ${value.size}`;
          price = end1Price;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'end2':
          label = 'END2';
          content = `${value.type} - ${value.size}`;
          price = end2Price;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'angle':
          label = 'ANGLE';
          content = value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'cutLengths':
          const totalLength = getTotalLength();
          const cutLengthPrice = totalLength * cleanPriceString(selectedHosePrice);
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>CUT LENGTHS</LabelItem>
              <BorderedItem 
                price={cutLengthPrice}
                isCutLengths={true}
                style={styles.cutLengthsContainer}
              >
                <View>
                  {value.map((cut, i) => (
                    <View key={i} style={styles.cutLengthRow}>
                      <NumberedCircle number={i + 1} />
                      <Text style={styles.cutLengthText}>{`${cut.length} ${cut.unit}`}</Text>
                    </View>
                  ))}
                </View>
              </BorderedItem>
            </View>
          );
        
        case 'hoseProtection':
          label = 'HOSE PROTECTION';
          content = value;
          price = value === 'NOT REQUIRED' ? 'NOT REQUIRED' : protectionCost;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        case 'pressureTest':
          label = 'PRESSURE TEST';
          content = value;
          price = value === 'Not Required' ? 'NOT REQUIRED' : pressureTestPrice;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );
        
        case 'shippingService':
          if (!value) return null;
          label = 'SHIPPING SERVICE';
          content = value;
          price = shippingCost > 0 ? shippingCost : 'FREE';
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem price={price}>
                {content}
              </BorderedItem>
            </View>
          );

        default:
          label = key.toUpperCase().replace('_', ' ');
          content = typeof value === 'object' ? 
            `${value.type} - ${value.size}` : value;
          return (
            <View key={index} style={styles.rowContainer}>
              <LabelItem>{label}</LabelItem>
              <BorderedItem>
                {content}
              </BorderedItem>
            </View>
          );
      }
    });
  };

  return (
    <View style={styles.orderDetailsContainer}>
      {renderOrderDetails()}
    </View>
  );
};

