import React from 'react';
import { View, TouchableOpacity, Text, StyleSheet, Platform } from 'react-native';
import { useNavigation } from '@react-navigation/native';
import { useCart } from '../context/CartContext';
import Svg, { Path, Circle } from 'react-native-svg';

const GlobalCartButton = ({ currentScreen }) => {
  const navigation = useNavigation();
  const { cartItems, isCartVisible, getCartItemCount } = useCart();
  
  // Don't show the cart button on the cart screen itself
  if (currentScreen === 'Cart' || !isCartVisible || cartItems.length === 0) {
    return null;
  }

  const handleCartPress = () => {
    navigation.navigate('Cart');
  };

  return (
    <TouchableOpacity 
      style={styles.globalCartButton}
      onPress={handleCartPress}
    >
      <View style={styles.cartIconContainer}>
        <Svg width="28" height="28" viewBox="0 0 24 24" fill="none">
          <Path 
            d="M9 22C9.55228 22 10 21.5523 10 21C10 20.4477 9.55228 20 9 20C8.44772 20 8 20.4477 8 21C8 21.5523 8.44772 22 9 22Z" 
            stroke="#4a4a4a" 
            strokeWidth="2" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
          <Path 
            d="M20 22C20.5523 22 21 21.5523 21 21C21 20.4477 20.5523 20 20 20C19.4477 20 19 20.4477 19 21C19 21.5523 19.4477 22 20 22Z" 
            stroke="#4a4a4a" 
            strokeWidth="2" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
          <Path 
            d="M1 1H5L7.68 14.39C7.77144 14.8504 8.02191 15.264 8.38755 15.5583C8.75318 15.8526 9.2107 16.009 9.68 16H19.4C19.8693 16.009 20.3268 15.8526 20.6925 15.5583C21.0581 15.264 21.3086 14.8504 21.4 14.39L23 6H6" 
            stroke="#4a4a4a" 
            strokeWidth="2" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
        </Svg>
        
        {/* Badge for item count */}
        {cartItems.length > 0 && (
          <View style={styles.badge}>
            <Text style={styles.badgeText}>
              {cartItems.length > 99 ? '99+' : cartItems.length}
            </Text>
          </View>
        )}
      </View>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  globalCartButton: {
    position: 'absolute',
    top: Platform.OS === 'ios' ? 60 : 20,
    right: 30,
    backgroundColor: 'white',
    width: 60,
    height: 60,
    borderRadius: 30,
    justifyContent: 'center',
    alignItems: 'center',
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 4,
    },
    shadowOpacity: 0.3,
    shadowRadius: 4.65,
    elevation: 8,
    zIndex: 1000,
  },
  cartIconContainer: {
    position: 'relative',
    justifyContent: 'center',
    alignItems: 'center',
  },
  badge: {
    position: 'absolute',
    top: -8,
    right: -8,
    backgroundColor: '#ffc100',
    borderRadius: 10,
    minWidth: 20,
    height: 20,
    justifyContent: 'center',
    alignItems: 'center',
    borderWidth: 2,
    borderColor: 'white',
  },
  badgeText: {
    color: '#4a4a4a',
    fontSize: 12,
    fontWeight: 'bold',
    textAlign: 'center',
  },
});

export default GlobalCartButton;