import React from 'react';
import { 
  View, 
  Text, 
  StyleSheet, 
  SafeAreaView, 
  FlatList, 
  TouchableOpacity, 
  Image,
  ScrollView,
  Platform,
  Dimensions,
} from 'react-native';
import { useNavigation } from '@react-navigation/native';
import Svg, { Path } from 'react-native-svg';
import { useCart } from '../context/CartContext';
import ScrollableGradientWrapper from '../components/ScrollableGradientWrapper';

const { width: SCREEN_WIDTH } = Dimensions.get('window');

const CartScreen = () => {
  const navigation = useNavigation();
  const { cartItems, removeFromCart, clearCart, getCartTotal } = useCart();

  const renderCartItem = ({ item, index }) => {
    const {
      selectedHose,
      cutLengths,
      end1Shape,
      end1Size,
      end2Shape,
      end2Size,
      selectedProtection,
      totalPrice,
      cartId,
    } = item;

    // Helper function to get total length from cut lengths
    const getTotalLength = () => {
      if (!cutLengths) return 0;
      return cutLengths.reduce((total, cut) => {
        if (!cut.length) return total;
        return total + (parseFloat(cut.length) / 1000); // Convert mm to meters
      }, 0);
    };

    return (
      <View style={styles.cartItemContainer}>
        <View style={styles.cartItemHeader}>
          <Text style={styles.cartItemHeaderText}>Item {index + 1}</Text>
          <TouchableOpacity 
            style={styles.removeButton}
            onPress={() => removeFromCart(cartId)}
          >
            <Svg width="20" height="20" viewBox="0 0 24 24" fill="none">
              <Path 
                d="M18 6L6 18M6 6l12 12" 
                stroke="#4a4a4a" 
                strokeWidth="2.5" 
                strokeLinecap="round" 
                strokeLinejoin="round"
              />
            </Svg>
          </TouchableOpacity>
        </View>
        
        <View style={styles.cartItemDetails}>
          {selectedHose && (
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Hose:</Text>
              <Text style={styles.detailValue}>{selectedHose.size}</Text>
            </View>
          )}
          
          {cutLengths && cutLengths.length > 0 && (
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Length:</Text>
              <Text style={styles.detailValue}>{getTotalLength().toFixed(2)} m</Text>
            </View>
          )}
          
          {end1Shape && end1Size && (
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>End 1:</Text>
              <Text style={styles.detailValue}>{`${end1Shape} - ${end1Size}`}</Text>
            </View>
          )}
          
          {end2Shape && end2Size && (
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>End 2:</Text>
              <Text style={styles.detailValue}>{`${end2Shape} - ${end2Size}`}</Text>
            </View>
          )}
          
          {selectedProtection && (
            <View style={styles.detailRow}>
              <Text style={styles.detailLabel}>Protection:</Text>
              <Text style={styles.detailValue}>{selectedProtection}</Text>
            </View>
          )}
        </View>
        
        <View style={styles.cartItemFooter}>
          <View style={styles.priceContainer}>
            <Text style={styles.priceLabel}>Price:</Text>
            <Text style={styles.priceValue}>
              A${totalPrice ? totalPrice.toFixed(2) : '0.00'}
            </Text>
          </View>
        </View>
      </View>
    );
  };

  return (
    <SafeAreaView style={styles.mainContainer}>
      {/* Floating Back Button */}
      <TouchableOpacity 
        style={styles.floatingBackButton}
        onPress={() => navigation.goBack()}
      >
        <Svg width="24" height="24" viewBox="0 0 24 24" fill="none">
          <Path 
            d="M19 12H5M12 19L5 12L12 5" 
            stroke="#4a4a4a" 
            strokeWidth="2.5" 
            strokeLinecap="round" 
            strokeLinejoin="round"
          />
        </Svg>
      </TouchableOpacity>

      <View style={styles.mainContentContainer}>
        <ScrollableGradientWrapper>
          <View style={styles.container}>
            <ScrollView contentContainerStyle={styles.scrollContent}>
              <View style={styles.logoContainer}>
                <Image
                  source={require('../assets/fluidpower_logo.gif')}
                  style={styles.logo}
                  resizeMode="contain"
                />
              </View>
              
              <View style={styles.titleContainer}>
                <Text style={styles.titleText}>YOUR CART</Text>
              </View>
              
              {cartItems.length === 0 ? (
                <View style={styles.emptyCartContainer}>
                  <Text style={styles.emptyCartText}>Your cart is empty</Text>
                  <TouchableOpacity 
                    style={styles.continueShoppingButton}
                    onPress={() => navigation.navigate('Welcome')}
                  >
                    <Text style={styles.continueShoppingButtonText}>CONTINUE SHOPPING</Text>
                  </TouchableOpacity>
                </View>
              ) : (
                <>
                  <FlatList
                    data={cartItems}
                    renderItem={renderCartItem}
                    keyExtractor={(item) => item.cartId.toString()}
                    style={styles.cartList}
                    scrollEnabled={false}
                    contentContainerStyle={styles.listContentContainer}
                  />
                  
                  <View style={styles.cartSummary}>
                    <View style={styles.cartTotalContainer}>
                      <Text style={styles.cartTotalLabel}>TOTAL</Text>
                      <Text style={styles.cartTotalValue}>
                        A${getCartTotal().toFixed(2)}
                      </Text>
                    </View>
                    
                    <View style={styles.shippingNoticeContainer}>
                      <Text style={styles.shippingNoticeText}>
                        *All our shipping costs are capped at A$ 12.85
                      </Text>
                    </View>
                    
                    <View style={styles.cartActions}>
                      <TouchableOpacity 
                        style={styles.clearCartButton}
                        onPress={clearCart}
                      >
                        <Text style={styles.buttonText}>CLEAR CART</Text>
                      </TouchableOpacity>
                      
                      <TouchableOpacity 
                        style={styles.checkoutButton}
                        onPress={() => {
                          // Create a combined cart item for shipping
                          const combinedCartData = {
                            cartItems,
                            cartTotal: getCartTotal(),
                            fromCart: true
                          };
                          
                          navigation.navigate('ShippingDetails', combinedCartData);
                        }}
                      >
                        <Text style={styles.buttonText}>PROCEED TO SHIPPING</Text>
                      </TouchableOpacity>
                    </View>
                    
                    <TouchableOpacity 
                      style={styles.continueShoppingButton}
                      onPress={() => navigation.goBack()}
                    >
                      <Text style={styles.continueShoppingButtonText}>CONTINUE SHOPPING</Text>
                    </TouchableOpacity>
                  </View>
                </>
              )}
            </ScrollView>
          </View>
        </ScrollableGradientWrapper>
      </View>
    </SafeAreaView>
  );
};

