import React, { createContext, useState, useContext, useEffect } from 'react';

// ============================================================================
// CART CONTEXT WITH LOCALSTORAGE INTEGRATION
// ============================================================================
// This cart context integrates with the website's localStorage to provide
// a unified cart experience across PWA and Next.js website.
// 
// localStorage Keys:
// - 'shopping-cart': Main cart data { open, items, timestamp }
// - 'cart-timestamp': Separate timestamp for expiration checks
//
// Cart items from PWA are tagged with type: 'pwa_order'
// ============================================================================

const CartContext = createContext({
  cartItems: [],
  isCartVisible: false,
  addToCart: () => {},
  removeFromCart: () => {},
  clearCart: () => {},
  getCartTotal: () => 0,
  getCartItemCount: () => 0,
});

export const useCart = () => useContext(CartContext);

// Constants
const CART_KEY = 'shopping-cart';
const TIMESTAMP_KEY = 'cart-timestamp';
const CART_EXPIRY_MS = 3600000; // 1 hour in milliseconds

export const CartProvider = ({ children }) => {
  const [cartItems, setCartItems] = useState([]);
  const [isCartVisible, setIsCartVisible] = useState(false);
  const isSyncingFromStorage = React.useRef(false);
  
  // ============================================================================
  // INITIALIZATION: Load cart from localStorage on mount
  // ============================================================================
  // ============================================================================
// INITIALIZATION: Load cart from localStorage on mount
// ============================================================================
useEffect(() => {
  if (typeof window === 'undefined') {
    return;
  }

  console.log('CartContext: Initializing from localStorage...');
  
  try {
    const storedCart = localStorage.getItem(CART_KEY);
    const storedTimestamp = localStorage.getItem(TIMESTAMP_KEY);
    
    if (!storedCart || !storedTimestamp) {
      console.log('CartContext: No existing cart found');
      sessionStorage.setItem('pwa-cart-initialized', 'true');
      return;
    }

    const cartData = JSON.parse(storedCart);
    const timestamp = parseInt(storedTimestamp, 10);
    const now = Date.now();
    const age = now - timestamp;

    console.log('CartContext: Found cart in localStorage', {
      itemCount: cartData.items?.length || 0,
      age: `${Math.round(age / 1000)}s`,
      expired: age > CART_EXPIRY_MS
    });

    if (age > CART_EXPIRY_MS) {
      console.log('CartContext: Cart expired, clearing');
      localStorage.removeItem(CART_KEY);
      localStorage.removeItem(TIMESTAMP_KEY);
      sessionStorage.setItem('pwa-cart-initialized', 'true');
      return;
    }

    //  FIX: Load ALL items from localStorage, not just PWA items
    // This preserves website items when PWA first launches
    if (cartData.items && Array.isArray(cartData.items)) {
      if (cartData.items.length > 0) {
        console.log('CartContext: Loading ALL items (website + PWA):', cartData.items.length, 'items');
        
        //  CRITICAL: Mark that we're syncing to prevent save loop
        isSyncingFromStorage.current = true;
        
        //  Load ALL items, regardless of their type (website or PWA)
        setCartItems(cartData.items);
        setIsCartVisible(true);
        
        console.log('CartContext: Items loaded:', {
          pwaItems: cartData.items.filter(item => item.type === 'pwa_order').length,
          websiteItems: cartData.items.filter(item => item.type !== 'pwa_order').length
        });
      } else {
        console.log('CartContext: Cart is empty');
      }
    }

    sessionStorage.setItem('pwa-cart-initialized', 'true');

  } catch (error) {
    console.error('CartContext: Error loading from localStorage:', error);
    try {
      localStorage.removeItem(CART_KEY);
      localStorage.removeItem(TIMESTAMP_KEY);
    } catch (clearError) {
      console.error('CartContext: Error clearing corrupted data:', clearError);
    }
    sessionStorage.setItem('pwa-cart-initialized', 'true');
  }
}, []);

  // ============================================================================
  // PERSISTENCE: Save cart to localStorage whenever it changes
  // ============================================================================
  useEffect(() => {
    if (typeof window === 'undefined') {
      return;
    }
  
    const hasInitialized = sessionStorage.getItem('pwa-cart-initialized');
    
    if (!hasInitialized) {
      console.log('CartContext: Not initialized yet, skipping save');
      return;
    }
  
    if (isSyncingFromStorage.current) {
      isSyncingFromStorage.current = false;
      return;
    }
  
    try {
      if (cartItems.length === 0) {
        localStorage.removeItem(CART_KEY);
        localStorage.removeItem(TIMESTAMP_KEY);
      } else {
        const cartData = {
          open: false,
          items: cartItems,  //  Save ALL items (website + PWA)
          timestamp: Date.now()
        };
  
        localStorage.setItem(CART_KEY, JSON.stringify(cartData));
        localStorage.setItem(TIMESTAMP_KEY, Date.now().toString());
      }
    } catch (error) {
      if (error.name === 'QuotaExceededError') {
        console.error('CartContext: localStorage quota exceeded!');
        alert('Cart storage is full. Please clear your browser cache.');
      } else {
        console.error('CartContext: Error saving to localStorage:', error);
      }
    }
  }, [cartItems]);

  // ============================================================================
  // SYNC WITH WEBSITE: Event-driven sync (no polling)
  // ============================================================================
  useEffect(() => {
    if (typeof window === 'undefined') {
      return;
    }

    // Listen for website cart changes
    const handleWebsiteCartChange = (event) => {
      try {
        const { items } = event.detail;
        const storageItems = items || [];
        const currentItems = cartItems;

        const storageItemCount = storageItems.length;
        const currentItemCount = currentItems.length;

        if (storageItemCount !== currentItemCount) {
          isSyncingFromStorage.current = true;
          setCartItems(storageItems);
        } else if (storageItemCount > 0) {
          const currentIds = currentItems.map(item => item.cartId).sort().join(',');
          const storageIds = storageItems.map(item => item.cartId).sort().join(',');
          
          if (currentIds !== storageIds) {
            isSyncingFromStorage.current = true;
            setCartItems(storageItems);
          }
        }
      } catch (error) {
        console.error('CartContext: Error handling website cart change:', error);
      }
    };

    window.addEventListener('website-cart-changed', handleWebsiteCartChange);

    return () => {
      window.removeEventListener('website-cart-changed', handleWebsiteCartChange);
    };
  }, [cartItems]);

  // ============================================================================
  // VISIBILITY EFFECT
  // ============================================================================
  useEffect(() => {
    const shouldBeVisible = cartItems.length > 0;
    if (shouldBeVisible !== isCartVisible) {
      setIsCartVisible(shouldBeVisible);
    }
  }, [cartItems.length, isCartVisible]);
  
  // ============================================================================
  // CART OPERATIONS
  // ============================================================================
  
  const addToCart = (item) => {
    
    setCartItems(prevItems => {
      const newItem = {
        ...item,
        cartId: item.cartId || Date.now(),
        type: item.type || 'pwa_order'
      };
      
      const newItems = [...prevItems, newItem];

      window.dispatchEvent(new CustomEvent('pwa-cart-item-added', {
        detail: { item: newItem }
      }));

      return newItems;
    });
  };
  
  const removeFromCart = (cartId) => {
    console.log('CartContext: Removing item:', cartId);
    
    setCartItems(prevItems => {
      return prevItems.filter(item => item.cartId !== cartId);
    });
  };
  
  const clearCart = () => {
    console.log('CartContext: Clearing cart');
    setCartItems([]);
  };
  
  const getCartTotal = () => {
    return cartItems.reduce((total, item) => {
      return total + (item.totalPrice || 0);
    }, 0);
  };

  const getCartItemCount = () => {
    return cartItems.length;
  };
  
  return (
    <CartContext.Provider
      value={{
        cartItems,
        isCartVisible,
        addToCart,
        removeFromCart,
        clearCart,
        getCartTotal,
        getCartItemCount,
      }}
    >
      {children}
    </CartContext.Provider>
  );
};

export default CartContext;